
import { Options, Vue } from "vue-class-component";
import qr from "./QrCustomiser.vue";
import { BrowserQRCodeReader } from "@zxing/browser";

import {
  ChecksumException,
  FormatException,
  NotFoundException,
} from "@zxing/library";

@Options({
  components: { qr },
  props: {
    msg: String,
  },
  data() {
    return {
      el: "#app",
      cameraPermission: "prompt",
      codeReader: null,
      isLastScanSuccess: null,
      camera: false,
    };
  },
  async mounted() {},
  methods: {
    toggleCamera() {
      if (!this.camera) {
        this.camera = true;
        this.startScanner();
      } else this.camera = false;
    },
    async previewFiles(event: { target: { files: any } }) {
      var url = URL.createObjectURL(event.target.files[0]);
      try {
        const resultImage = await this.codeReader.decodeFromImageUrl(url);
        this.$emit("onQrCodeChange", resultImage.text);
        console.log(resultImage);
        this.isLastScanSuccess = true;
        this.$gtag.event("newScan", { method: "fromCamera" });
      } catch (e) {
        console.log(e);
        this.isLastScanSuccess = false;
      }
    },

    async decode() {},
    startScanner() {
      this.codeReader = new BrowserQRCodeReader();
      this.codeReader.decodeFromVideoDevice(
        undefined,
        "webcam-preview",
        (result: { getText: () => any }, err: any) => {
          if (result) {
            // properly decoded qr code
            console.log("Found QR code!", result);
            this.$emit("onQrCodeChange", result.getText());
            console.log(result.getText());
            this.isLastScanSuccess = true;
            this.camera = false;
            var qre = document.getElementById("customise");
            if (qre) qre.scrollIntoView();
          }

          if (err) {
            if (err instanceof NotFoundException) {
              console.log("No QR code found.");
            }
            if (err instanceof ChecksumException) {
              console.log(
                "A code was found, but it's read value was not valid."
              );
            }
            if (err instanceof FormatException) {
              console.log("A code was found, but it was in a invalid format.");
            }
          }
        }
      );
    },

    async askCameraPerm() {
      var context = this;
      navigator.mediaDevices
        .getUserMedia({ video: true })
        .then(function () {
          context.cameraPermission = "granted";
          context.initScanner();
        })
        .catch(function (err) {
          context.cameraPermission = "denied";
          console.log(err);
        });
    },
  },
})
export default class HelloWorld extends Vue {
  msg!: string;
}
